import Breakcrum from "@/components/Breakcrum";
import Container from "@/components/Container";
import { Button } from "@/components/ui/button";
import { Checkbox } from "@/components/ui/checkbox";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import React from "react";

const Lookup = () => {
  return (
    <>
        <Breakcrum title="Tra cứu đơn hàng" description="Tra cứu thông tin đơn hàng" />
        <div className="py-5 md:py-10">
            <Container>
                <div className="grid grid-cols-3 gap-10">
                  <div className="col-span-3 md:col-span-1">
                    <div className="p-6 shadow-md rounded-md space-y-4 bg-light_brownish/10">
                      <h3>Kiểm tra đơn hàng của bạn</h3>
                      <div className="flex items-center gap-4">
                        <div className="flex items-center gap-2">
                          <Checkbox id='email' />
                          <Label htmlFor={'email'} className="text-darkGray text-sm font-normal cursor-pointer flex items-center gap-2">Email</Label>
                        </div>
                        <div className="flex items-center gap-2">
                          <Checkbox id='phone' />
                          <Label htmlFor={'phone'} className="text-darkGray text-sm font-normal cursor-pointer flex items-center gap-2">Số điện thoại</Label>
                        </div>
                        <div className="flex items-center gap-2">
                          <Checkbox id='emailandphone' />
                          <Label htmlFor={'emailandphone'} className="text-darkGray text-sm font-normal cursor-pointer flex items-center gap-2">Email va Số điện thoại</Label>
                        </div>
                      </div>
                      <Input 
                        placeholder="Nhập số điện thoại hoặc email người nhận"
                        className="bg-white"
                      />
                      <div className="flex items-center justify-end">
                        <Button className="h-[30px]">Kiểm tra</Button>
                      </div>
                    </div>
                  </div>
                  <div className="col-span-3 md:col-span-2">
                    <div className="p-6 shadow-md rounded-md space-y-4">
                      <div className="grid grid-cols-3 gap-4">
                        <div className="col-span-3">
                          <span className="text-lg">Mã đơn hàng: 1</span>
                        </div>
                        <div className="col-span-2 flex flex-col text-sm">
                          <span>Khách hàng: Minh Nghĩa</span>
                          <span>Số điện thoai: 012345678</span>
                          <span>Email: nghia.com</span>
                          <span>Ngày mua: 01/01/2023</span>
                          <span>Địa chỉ giao hàng: 123, Hà Nội</span>
                        </div>
                        <div className="col-span-1 flex flex-col text-sm">
                          <span>Tổng giá sản phẩm: <span className="text-base ">1.000.000</span></span>
                          <span>Phí vận chuyển: <span className="text-base ">1.000.000</span></span>
                          <span>Tổng tiền: <span className="text-base font-medium text-red-600">1.000.000</span></span>
                        </div>
                        <div className="col-span-3 flex flex-col text-sm">
                          <span>Trạng thái thái toán: Đa hóa</span>
                          <span>Trạng thái đơn hàng: Đa hóa</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
            </Container>
        </div>
    </>
  );
};

export default Lookup;
